.shieldDao {
  background-color: #fff;
  padding: 200px 0;
  position: relative;
  color: #000;
  text-align: center;
  max-width: 1040px;
  margin: 0 auto;

  img {
    height: 552px;
    width: auto;
  }

  .bgImg {
    background-image: url('../../../assets//shield-dao-bg.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;

    & > div {
      h3 {
        font-size: 60px;
        font-family: Gilroy-Bold, Gilroy;
        font-weight: bold;
        color: #000000;
        line-height: 74px;
      }
      h3 + p {
        font-size: 18px;
        font-family: Gilroy-Light, Gilroy;
        font-weight: 300;
        color: #000000;
        line-height: 22px;
        margin-top: 24px;
      }

      span {
        font-size: 18px;
        font-family: Gilroy-Light, Gilroy;
        font-weight: 300;
        line-height: 22px;
        margin-top: 30px;
      }
    }
  }
}

