@import '../variables.less';
@import '../../common.less';

:global(.mobile) {
  .joinCommunity {
    padding: 30px 15px 100px;
    h3 {
      font-size: 20px;
      opacity: 0.8;
      line-height: 1;
    }

    h3 + p {
      margin-bottom: 0px;
      margin-top: 16px;
      font-size: 13px;
    }

    i {
      font-size: 28px;
      margin-top: 50px;
    }

    .icon {
      width: 28px;
      margin-top: 50px;
    }
  }
}
.joinCommunity {
  text-align: center;
  color: #fff;
  background-color: #1346ff;
  padding: 120px 0;

  & > h3 {
    max-width: 800px;
    font-size: 60px;
    margin: 0 auto;
    font-family: Gilroy-Bold, Gilroy;
    font-weight: bold;
    color: #ffffff;
    line-height: 74px;
  }

  i {
    color: #fff;
    font-size: 55px;
    margin-top: 60px;
  }

  .icon {
    width: 60px;
    margin-top: 60px;
  }
}

