.backedBy {
  background: #fff;
  padding: 120px;

  h2 {
    font-size: 60px;
    font-family: Gilroy-Bold, Gilroy;
    font-weight: bold;
    color: #000000;
    line-height: 74px;
    text-align: center;
    margin: 0 0 45px;
  }

  .partnerWpr {
    text-align: center;
  }

  .partnerItem {
    width: 120px;
    height: 50px;
    display: inline-block;
    margin: 35px 0;
  }

  img {
    height: 100%;
  }
}

