@import '../../../variable.less';

.asFeature {
  background: #fff;
  padding-bottom: 120px;
  .featureContent {
    max-width: @max-content-width;
    padding: 120px 0 96px;
    margin: 0 auto;
    background: #fafafa;
    text-align: center;
  }
  h2 {
    font-size: 60px;
    font-family: Gilroy-Bold, Gilroy;
    font-weight: bold;
    color: #000000;
    line-height: 74px;
    text-align: center;
    margin: 0;
  }

  .desc {
    font-size: 30px;
    font-family: Gilroy-Bold, Gilroy;
    font-weight: bold;
    color: #000000;
    line-height: 37px;
    margin-bottom: 20px;
  }

  a.link {
    font-size: 60px;
    font-family: Helvetica;
    color: #000000;
    line-height: 72px;
  }

  .partnerItem {
    width: 120px;
    height: 50px;
    display: inline-block;
    margin: 60px 0 40px;
  }

  img {
    height: 100%;
  }
}

