@import 'font.css';
body {
  -webkit-font-smoothing: antialiased;
  background-color: #f9fafc !important;
  font-family: Gilroy-Heavy, Gilroy proxima-nova, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Wenquanyi Micro Hei, WenQuanYi Micro Hei Mono, WenQuanYi Zen Hei, Apple LiGothic Medium, SimHei, ST Heiti, WenQuanYi Zen Hei Sharp !important;
}
/** Descriptions */
.ant-descriptions-item > span {
  width: 50%;
}
.ant-descriptions-item > span:nth-child(1) {
  text-align: left;
  font-size: 14px;
  font-weight: 500;
  color: #a6a6a6;
  line-height: 17px;
}
.ant-descriptions-item > span:nth-child(2) {
  text-align: right;
  font-size: 14px;
  font-weight: 500;
  color: #333333;
  line-height: 17px;
}
/** Table */
.ant-table-thead th {
  background-color: #fff !important;
}
.ant-table-tbody tr:hover td {
  background-color: #f6f7f9 !important;
}
.ant-table-tbody td,
.ant-table-thead th,
.ant-table-thead td {
  border: 0;
}
.ant-table-thead .ant-table-column-title {
  font-size: 14px;
  color: #808080;
  font-weight: normal;
}
/** Modal */
.ant-modal-root .ant-modal-title {
  font-size: 24px;
  text-align: center;
  font-weight: bold;
}
.ant-modal-root .ant-modal-header {
  padding: 36px 0 4px;
  border-bottom: 0;
}
.ant-modal-root .ant-modal-footer {
  border-top: 0;
  text-align: center;
  padding-bottom: 40px;
}
.ant-modal-root .ant-modal-footer button {
  width: 80%;
}
.ant-modal-root .ant-modal-footer button + button {
  margin-left: 16px !important;
}
/** Dropdown */
.ant-dropdown.ant-dropdown-placement-topRight ul {
  text-align: right;
}
.ant-dropdown.ant-dropdown-placement-topRight li {
  padding: 10px 15px;
}
.ant-dropdown.ant-dropdown-placement-topRight li a {
  font-size: 16px;
  font-weight: bold;
  color: #303236;
}
/** Drawer */
.ant-drawer-right .ant-drawer-content .ant-drawer-body {
  padding: 10px 0 0;
}
.ant-drawer-right .ant-drawer-content .ant-drawer-body > ul {
  background-color: #fff;
  font-size: 16px;
  font-weight: bold;
  color: #303236;
}
.ant-drawer-right .ant-drawer-content .ant-drawer-body a,
.ant-drawer-right .ant-drawer-content .ant-drawer-body .ant-menu,
.ant-drawer-right .ant-drawer-content .ant-drawer-body .ant-menu-item:active,
.ant-drawer-right .ant-drawer-content .ant-drawer-body .ant-menu-submenu-title:active,
.ant-drawer-right .ant-drawer-content .ant-drawer-body .ant-menu-submenu > .ant-menu,
.ant-drawer-right .ant-drawer-content .ant-drawer-body .ant-menu-item {
  background-color: #fff;
  color: #303236;
}
.ant-drawer-right .ant-drawer-content .ant-drawer-body .ant-menu-submenu-selected,
.ant-drawer-right .ant-drawer-content .ant-drawer-body .ant-menu-submenu-title:hover {
  color: #303236;
}
.ant-drawer-right .ant-drawer-content .ant-drawer-body .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background-color: #fff;
  color: #303236;
}
.ant-drawer-right .ant-drawer-content .ant-drawer-body .ant-menu-submenu-inline > .ant-menu-submenu-title .ant-menu-submenu-arrow::before,
.ant-drawer-right .ant-drawer-content .ant-drawer-body .ant-menu-submenu-inline > .ant-menu-submenu-title .ant-menu-submenu-arrow::after {
  background: #1346ff;
}
/** Progress */
.ant-progress-inner {
  background-color: #e7ebf4;
}
.ant-progress-inner,
.ant-progress-bg {
  border-radius: 0 !important;
}
.ant-progress-circle .ant-progress-text {
  font-size: 16px;
  font-weight: bold;
  color: #1346ff !important;
}
.ant-progress-circle svg {
  transform: rotateY(180deg);
}
/** Select */
.ant-row.ant-form-item {
  margin-bottom: 16px;
}
.ant-menu-submenu > .ant-menu {
  background: #fff !important;
}
.ant-menu-submenu > .ant-menu .ant-menu-item > a {
  color: #333;
}
.ant-menu-submenu > .ant-menu .ant-menu-item-selected > a,
.ant-menu-submenu > .ant-menu .ant-menu-item-selected > a:hover {
  color: #fff;
}
.ant-menu.ant-menu-horizontal > .ant-menu-submenu-selected,
.ant-menu.ant-menu-horizontal > .ant-menu-submenu:hover {
  border-color: #1346ff;
}
/** Tabs */
.dds-tabs .ant-tabs-bar {
  border: 0;
}
.dds-tabs > .ant-tabs-content .ant-tabs-tabpane {
  background-color: #fff;
  border-radius: 8px;
}
.dds-tabs.miningTabs > .ant-tabs-bar .ant-tabs-tab {
  width: 33.3%;
}
.dds-tabs > .ant-tabs-bar {
  margin-bottom: 25px;
}
.dds-tabs > .ant-tabs-bar .ant-tabs-nav {
  width: 100%;
}
.dds-tabs > .ant-tabs-bar .ant-tabs-nav .ant-tabs-tab-active {
  background: #e7ebf4;
  border-radius: 9px;
  margin-right: 0;
}
.dds-tabs > .ant-tabs-bar .ant-tabs-tab {
  width: 50%;
  text-align: center;
  margin: 0;
  line-height: 43px;
  font-size: 18px;
  font-weight: bold;
  color: #333333;
}
.dds-tabs > .ant-tabs-bar .ant-tabs-ink-bar {
  visibility: hidden;
}
.mobile .dds-tabs > .ant-tabs-bar .ant-tabs-tab {
  font-size: 14px;
  line-height: 50px;
  padding: 0;
}
.mobile button {
  border-radius: 4px;
}
.ant-drawer-content .ant-drawer-header {
  padding: 24px 15px;
  border-bottom: 0;
}
.ant-drawer-content .ant-drawer-header .ant-drawer-title {
  font-size: 16px;
  font-weight: bold;
  color: #333333;
}
.ant-drawer-content .ant-drawer-body {
  padding: 0 15px 24px;
}
.ant-drawer-content .ant-drawer-body h4 {
  font-size: 16px;
  font-weight: bold;
  color: #333333;
  margin-bottom: 15px;
}
.font-w-bold {
  font-weight: bold;
}
.text-center {
  text-align: center;
}
.color-3 {
  color: #333;
}
button.buttonGreen {
  border-color: #15b384;
  background-color: #15b384;
}
button.buttonGreen:focus,
button.buttonGreen:hover {
  background-color: #15b384;
  border-color: #15b384;
}
button.buttonRed {
  border-color: #f55858;
  background-color: #f55858;
}
button.buttonRed:focus,
button.buttonRed:hover {
  background-color: #f55858;
  border-color: #f55858;
}
.submenu-dialog {
  max-width: 382px;
  background: #fff;
  font-size: 14px;
  color: #333333;
  line-height: 17px;
}
.submenu-dialog .top {
  display: flex;
  flex-direction: column;
  padding: 25px;
  border-bottom: 1px solid #f3f3f3;
  font-family: Gilroy-Bold, Gilroy;
}
.submenu-dialog .top .content {
  font-family: Gilroy-Medium, Gilroy;
  font-weight: 500;
  color: #666666;
  margin-top: 10px;
}
.submenu-dialog .bottom {
  display: flex;
  justify-content: space-around;
}
.submenu-dialog .link {
  color: #333;
  z-index: 1;
  width: 50%;
  padding: 12px 0;
  text-align: center;
}
.submenu-dialog .link:first-child {
  border-right: 1px solid #f3f3f3;
}
.submenu-dialog .link:hover {
  background-color: #f8f8f8;
}
.submenu-dialog .mobile-link {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
