:global(.mobile) {
  .banner {
    padding: 52px 30px 64px;
    text-align: center;
    height: 100vh;
    background: #fff;
    color: #272727;
    .content {
      padding-left: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 80%;
      transform: translate(-50%, -50%);
      .read {
        margin: 0 auto;
      }
      h2 {
        font-size: 36px;
      }
    }
  }
}

@header-font-color: #272727;

.banner {
  background-color: #fff;
  color: #272727;
  padding: 220px 0;
  position: relative;
  background: #fff url('../../assets/imgs/adsImage.png') no-repeat;
  background-position: 90% 60%;
  background-size: 680px;

  & > .contentWpr {
    margin: 0 auto;
    max-width: 1280px;
  }
  .content {
    width: 640px;
  }
  h2 {
    font-size: 60px;
    font-family: Gilroy-Bold, Gilroy;
    font-weight: normal;
    color: #272727;
    line-height: 66px;
  }
  h2 + p {
    margin-top: 24px;
    font-size: 20px;
    font-family: Gilroy-Regular, Gilroy;
    font-weight: 400;
    color: #000000;
    line-height: 24px;
    opacity: 0.6;
  }
  .spot,
  .read {
    width: 170px;
    font-size: 16px;
    font-family: Gilroy-Bold, Gilroy;
  }
  .spot {
    height: 50px;
    border-radius: 2px;
    margin: 24px 0 0;
  }

  .read {
    margin-left: 20px;
  }
}

